import React, {useEffect, useState} from "react";
import { Link, graphql } from "gatsby";
import rehypeReact from "rehype-react";
import ReactGA from 'react-ga';
import { DiscussionEmbed } from "disqus-react";
import LazyLoad from 'react-lazyload';

import SEO from "../../components/SEO";
import Layout from "../../components/layout";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import Youtube from './components/Youtube';
import ImageComparison from './components/ImageComparison';
import AnalyticsLink from './components/OutboundLink';
import ImageCarousel from "./components/ImageCarousel";
import Share from "./components/Share";

import "./styles.scss"; 

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: {
    "img": LazyLoadImage,
    "img-carousel": ImageCarousel,
    "youtube": Youtube,
    "img-compare": ImageComparison,
    "a": AnalyticsLink,
  },
}).Compiler;

export default function Template({
  data
}) {
  const [start] = useState(new Date());
  const { relatedPosts, blog, site } = data;
  const { excerpt, fields, frontmatter, htmlAst, wordCount, id } = blog;

  useEffect(() => {
    return () => {
      const cur = new Date();
      const diff = cur.getMilliseconds() - start.getMilliseconds();
      ReactGA.timing({
        category: 'TimeOnBlog',
        variable: 'time',
        value: diff, // in milliseconds
        label: id,
      });
    };
  });

  const disqusConfig = {
    shortname: "techwaifu",
    config: { identifier: id, title: frontmatter.title },
  };

  const metadata = JSON.stringify({
    "@context": "https://schema.org",
    "@type": "BlogPosting",
    "headline": frontmatter.title,
    "image": frontmatter.cover_photo,
    "editor": "Mai Nguyen",
    "genre": frontmatter.related_tag,
    "keywords": frontmatter.tags.join(", "),
    "wordcount": `"${wordCount.words}"`,
    "publisher": "Mai Nguyen",
    "url": `${site.siteMetadata.siteUrl}/${id}`,
    "datePublished": frontmatter.date,
    "dateCreated": frontmatter.date,
    "dateModified": frontmatter.date,
    "description": `"${excerpt}"`,
    "articleBody": `"${excerpt}"`,
    "author": {
      "@type": "Person",
      "name": "Mai Nguyen"
    },
		"mainEntityOfPage": "True"
  });
  return (
    <Layout>
      <SEO
        title={frontmatter.title}
        img={frontmatter.cover_photo}
        description={excerpt}
        keywords={frontmatter.tags}
        urlPath={`${id}`}
        metadata={metadata}
      />
      <section className="blog-post-container">
        <div className="blog-post">
          <h1 className="Title">{frontmatter.title}</h1>
          <div className="Date">Published: {frontmatter.date}</div>
          <div className="TimeToRead">{fields.readingTime.text}</div>
          <LazyLoadImage src={frontmatter.cover_photo} alt={frontmatter.title} />

          <div className="blog-post-content">
            {renderAst(htmlAst)}
          </div>

          <div className="blog-post-tags">
            {frontmatter.tags.map((tag) => {
              return (<Link key={tag} className="blogTag" to={`/${tag.toLowerCase()}`}>#{tag}</Link>);
            })}
          </div>
        </div>

        {relatedPosts.edges.length > 1 &&
          <div className="RelatedPostsCtr">
            <h2>Related Posts</h2>
            <div className="RelatedPosts">
              {relatedPosts.edges.map(({node}) => {
                const {cover_photo, slug, title} = node.frontmatter;
                return (
                  <Link className="RelatedPostCard" to={`/${slug}`}  key={slug}>
                    <article>
                      <div className="RelatedPostImgCtr">
                        <LazyLoadImage src={cover_photo} alt={title} />
                      </div>
                      <div className="RelatedPostTitleCtr">
                        {title}
                      </div>
                    </article>
                  </Link>
                );
              })}
            </div>
          </div>
        }

        <div className="CommentsSection">
          <LazyLoad height={200} offset={200}>
            <DiscussionEmbed {...disqusConfig} />
          </LazyLoad>
        </div>
      </section>

    </Layout>
  )
}

export const pageQuery = graphql`
  query($slug: String!, $relatedTag: String!) {
    site {
      siteMetadata {
        siteUrl
      }
    }
    blog: markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      htmlAst
      id
      excerpt
      frontmatter {
        date(formatString: "MMMM D, YYYY")
        slug
        title
        tags
        cover_photo
        related_tag
      }
      wordCount {
        words
      }
      fields {
        readingTime {
          text
        }
      }
    }
    relatedPosts: allMarkdownRemark(
      limit: 3,
      filter: {
        frontmatter:{tags:{in: [$relatedTag]}, slug:{ne:$slug} }
      }
    ) {
      edges {
        node {
          frontmatter {
            slug
            title
            cover_photo
          }
        }
      }
    }
  }
`